body {
  height: 100vh; width: 100vw;
  background-color: var(--color-1);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  touch-action: manipulation;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

:root {
  /* color */
  --color-1:#19171b;
  --color-2:#9e8123;
  --color-3:#2f2921;
  --color-4:#563a17;
  --color-5:#s;

  /* clop */
  --clip-t-to-b   :inset(   0    0 100%    0); /* 上→下 */
  --clip-b-to-t   :inset(100%    0    0    0); /* 下→上 */
  --clip-l-to-r   :inset(   0 100%    0    0); /* 左→右 */
  --clip-r-to-l   :inset(   0    0    0 100%); /* 右→左 */
  --clip-c-to-tb  :inset(100%    0 100%    0); /* 内→上下 */
  --clip-c-to-lr  :inset(   0 100%    0 100%); /* 内→左右 */
  --clip-c-to-o   :inset( 50%  50%  50%  50%); /* 内→外 */
  --clip-on       :inset(100% 100% 100% 100%); /* 非表示 */
  --clip-off      :inset(   0    0    0    0); /* 全表示 */
  --clip-out-on  :circle(  0%  at  50%  50%); /* 内→外非表示 */
  --clip-out-off :circle(100%  at  50%  50%); /* 内→外全表示 */
}

.main {
  height: 650px; width: 350px;
  position: relative;
  background-color: var(--color-1);
}



.layer-t-1, .layer-b-1, .layer-l-1, .layer-r-1 {
  position: absolute;
  overflow: hidden;
  transition: 1.5s;

  .a, .b, .c {
    position: absolute;
    border: solid 5px var(--color-2);
  }

  &.layer-t-1.load, &.layer-b-1.load { width:  350px;}
  &.layer-l-1.load, &.layer-r-1.load { height: 650px;}

  &.layer-t-1 {
    height: 50px; width: 0px; top: 0px; left: 0px; z-index: 1;
    .a, .b, .c { border-image: linear-gradient(90deg, var(--color-2), var(--color-3), var(--color-2)) 1;}

    .a, .c { border-top: none;}
    .b     { border-bottom: none;}
    .c     { border-left: none;}
    .a     { border-right: none;}

    .a { height: 35px; width:  50px; top: 10px; left:  10px;}
    .b { height: 35px; width: 220px; top: 10px; left:  60px;}
    .c { height: 35px; width:  50px; top: 10px; left: 285px;}
  }

  &.layer-b-1 {
    height: 50px; width: 0px; bottom: 0px; right: 0px; z-index: 1;
    .a, .b, .c { border-image: linear-gradient(90deg, var(--color-2), var(--color-3), var(--color-2)) 1;}

    .b     { border-top: none;}
    .a, .c { border-bottom: none;}
    .c     { border-left: none;}
    .a     { border-right: none;}

    .a { height: 35px; width:  50px; bottom: 10px; right: 285px;}
    .b { height: 35px; width: 220px; bottom: 10px; right:  60px;}
    .c { height: 35px; width:  50px; bottom: 10px; right:  10px;}
  }

  &.layer-l-1 {
    height: 0px; width: 50px; bottom: 0px; left: 0px; z-index: 2;
    .b { border-image: linear-gradient(180deg, var(--color-2), var(--color-3), var(--color-2)) 1;}

    .c     { border-top: none;}
    .a     { border-bottom: none;}
    .a, .c { border-left: none;}
    .b     { border-right: none;}

    .a { height:  50px; width: 35px; bottom: 585px; left: 10px;}
    .b { height: 520px; width: 35px; bottom:  60px; left: 10px;}
    .c { height:  50px; width: 35px; bottom:  10px; left: 10px;}
  }

  &.layer-r-1 {
    height: 0px; width: 50px; top: 0px; right: 0px; z-index: 2;
    .b { border-image: linear-gradient(180deg, var(--color-2), var(--color-3), var(--color-2)) 1;}

    .c     { border-top: none;}
    .a     { border-bottom: none;}
    .b     { border-left: none;}
    .a, .c { border-right: none;}

    .a { height:  50px; width: 35px; top:  10px; right: 10px;}
    .b { height: 520px; width: 35px; top:  60px; right: 10px;}
    .c { height:  50px; width: 35px; top: 585px; right: 10px;}
  }
}


